.dice-slider {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 20px;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  border-radius: 6px;
  -webkit-appearance: none;
  width: 100%;
  background: #1c2538 -webkit-gradient(linear, left top, right top, color-stop(0.5, #59b2e1), color-stop(0.5, #1c2538));
}

.dice-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  opacity: 1;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 6px;
  background-color: #f7f7f7;
}

.dice-slider::-moz-range-thumb {
  -webkit-appearance: none;
  opacity: 1;
  z-index: -1;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 6px;
  background-color: #f7f7f7;
}

.dice-slider::-moz-range-track {
  background: #f85d5d;
  height: 20px;
  border-radius: 20px;
}

.dice-slider::-moz-range-progress {
  background: #3ecf8e;
  height: 20px;
  border-radius: 20px;
}

.dice-slider.reverse::-moz-range-track {
  background: #3ecf8e;
}

.dice-slider.reverse::-moz-range-progress {
  background: #f85d5d;
}

.dice-autobetting.dropdown-menu {
  height: 85px;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  opacity: 0;
  display: none;
  width: 340px;
  color: #fff;
  -webkit-transform: translateY(-100%);
  transform: translateY(-100%);
}

.dice-roll-wrap.open .dropdown-menu {
  height: 370px;
  opacity: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

