@-webkit-keyframes spin {
  0% {
    -webkit-transform: translateZ(-100px) rotateX(0deg) rotateY(0deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(0deg) rotateY(0deg) rotate(0deg);
  }

  16% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(180deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(180deg) rotate(0deg);
  }

  33% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(90deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(1turn) rotateY(90deg) rotate(180deg);
  }

  50% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }

  66% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(1turn)
      rotate(270deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(1turn) rotate(270deg);
  }

  83% {
    -webkit-transform: translateZ(-100px) rotateX(270deg) rotateY(180deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(270deg) rotateY(180deg) rotate(180deg);
  }

  to {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: translateZ(-100px) rotateX(0deg) rotateY(0deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(0deg) rotateY(0deg) rotate(0deg);
  }

  16% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(180deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(180deg) rotate(0deg);
  }

  33% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(90deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(1turn) rotateY(90deg) rotate(180deg);
  }

  50% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }

  66% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(1turn)
      rotate(270deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(1turn) rotate(270deg);
  }

  83% {
    -webkit-transform: translateZ(-100px) rotateX(270deg) rotateY(180deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(270deg) rotateY(180deg) rotate(180deg);
  }

  to {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }
}

@-webkit-keyframes spin-duplicate {
  0% {
    -webkit-transform: translateZ(-100px) rotateX(0deg) rotateY(0deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(0deg) rotateY(0deg) rotate(0deg);
  }

  16% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(180deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(180deg) rotate(0deg);
  }

  33% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(90deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(1turn) rotateY(90deg) rotate(180deg);
  }

  50% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }

  66% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(1turn)
      rotate(270deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(1turn) rotate(270deg);
  }

  83% {
    -webkit-transform: translateZ(-100px) rotateX(270deg) rotateY(180deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(270deg) rotateY(180deg) rotate(180deg);
  }

  to {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }
}

@keyframes spin-duplicate {
  0% {
    -webkit-transform: translateZ(-100px) rotateX(0deg) rotateY(0deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(0deg) rotateY(0deg) rotate(0deg);
  }

  16% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(180deg)
      rotate(0deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(180deg) rotate(0deg);
  }

  33% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(90deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(1turn) rotateY(90deg) rotate(180deg);
  }

  50% {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }

  66% {
    -webkit-transform: translateZ(-100px) rotateX(180deg) rotateY(1turn)
      rotate(270deg);
    transform: translateZ(-100px) rotateX(180deg) rotateY(1turn) rotate(270deg);
  }

  83% {
    -webkit-transform: translateZ(-100px) rotateX(270deg) rotateY(180deg)
      rotate(180deg);
    transform: translateZ(-100px) rotateX(270deg) rotateY(180deg) rotate(180deg);
  }

  to {
    -webkit-transform: translateZ(-100px) rotateX(1turn) rotateY(1turn)
      rotate(1turn);
    transform: translateZ(-100px) rotateX(1turn) rotateY(1turn) rotate(1turn);
  }
}

#dice span {
  position: absolute;
  margin: 50px 0 0 50px;
  display: block;
  font-size: 2.5em;
  padding: 5px;
}

#dice {
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  transition: all 6s linear;
  transform: translateZ(-100px) rotateX(0deg) rotateY(0deg) rotate(0deg);
  // -webkit-animation: spin 50s infinite linear;
  animation: spin-duplicate 50s infinite linear;
  // animation: spin 50s linear;
}

#dice,
.side {
  position: absolute;
  width: 100px;
  height: 100px;
}

.side {
  background: #fff;
  -webkit-box-shadow: inset 0 0 20px #ccc;
  box-shadow: inset 0 0 20px #ccc;
  border-radius: 20px;
}

#dice .cover,
#dice .inner {
  background: #e0e0e0;
  -webkit-box-shadow: none;
  box-shadow: none;
}

#dice .cover {
  border-radius: 0;
  -webkit-transform: translateZ(0);
  transform: translateZ(0);
}

#dice .cover.x {
  -webkit-transform: rotateY(90deg);
  transform: rotateY(90deg);
}

#dice .cover.z {
  -webkit-transform: rotateX(90deg);
  transform: rotateX(90deg);
}

#dice .front {
  -webkit-transform: translateZ(50px);
  transform: translateZ(50px);
}

#dice .front.inner {
  -webkit-transform: translateZ(49px);
  transform: translateZ(49px);
}

#dice .back {
  -webkit-transform: rotateX(-180deg) translateZ(50px);
  transform: rotateX(-180deg) translateZ(50px);
}

#dice .back.inner {
  -webkit-transform: rotateX(-180deg) translateZ(49px);
  transform: rotateX(-180deg) translateZ(49px);
}

#dice .right {
  -webkit-transform: rotateY(90deg) translateZ(50px);
  transform: rotateY(90deg) translateZ(50px);
}

#dice .right.inner {
  -webkit-transform: rotateY(90deg) translateZ(48px);
  transform: rotateY(90deg) translateZ(48px);
}

#dice .left {
  -webkit-transform: rotateY(-90deg) translateZ(50px);
  transform: rotateY(-90deg) translateZ(50px);
}

#dice .left.inner {
  -webkit-transform: rotateY(-90deg) translateZ(49px);
  transform: rotateY(-90deg) translateZ(49px);
}

#dice .top {
  -webkit-transform: rotateX(90deg) translateZ(50px);
  transform: rotateX(90deg) translateZ(50px);
}

#dice .top.inner {
  -webkit-transform: rotateX(90deg) translateZ(49px);
  transform: rotateX(90deg) translateZ(49px);
}

#dice .bottom {
  -webkit-transform: rotateX(-90deg) translateZ(50px);
  transform: rotateX(-90deg) translateZ(50px);
}

#dice .bottom.inner {
  -webkit-transform: rotateX(-90deg) translateZ(49px);
  transform: rotateX(-90deg) translateZ(49px);
}

.dot {
  position: absolute;
  width: 23px;
  height: 23px;
  border-radius: 11px;
  background: #444;
  -webkit-box-shadow: inset 3px 0 5px #000;
  box-shadow: inset 3px 0 5px #000;
}

.dot.center {
  margin: 38px 0 0 38px;
}

.dot.dtop {
  margin-top: 10px;
}

.dot.dleft {
  margin-left: 67px;
}

.dot.dright {
  margin-left: 10px;
}

.dot.dbottom {
  margin-top: 67px;
}

.dot.center.dleft {
  margin: 38px 0 0 10px;
}

.dot.center.dright {
  margin: 38px 0 0 67px;
}
